import { isStringNullish } from '@/utils/stringUtils'
import { computed, ref, readonly, Ref, ComputedRef } from 'vue'

interface IAppIdComposable {
  appId: Ref<string>
  appIdUnassigned: ComputedRef<boolean>
  updateAppId: (newAppId: string) => void
}

const appId = ref<string>('')

const appIdUnassigned = computed<boolean>(() => isStringNullish(appId.value))

function updateAppId(newAppId: string): void {
  appId.value = newAppId
}

export function useAppId(): IAppIdComposable {
  return {
    appId: readonly(appId),
    appIdUnassigned,
    updateAppId,
  }
}
