<template>
  <div class="checkbox-container" :class="{ readonly: readonly }">
    <input
      :class="toggleSwitch ? 'passage-toggle-switch' : 'passage-checkbox'"
      type="checkbox"
      v-model="inputValue"
      ref="inputRef"
      @change.prevent="changeEvent"
      :readonly="readonly"
    />
    <div class="checkbox-label" :class="{ 'toggle-switch-label': toggleSwitch }" @click.prevent="setValue">
      {{ label }}
    </div>
  </div>
</template>
<script lang="ts">
import { defineComponent, computed, ref } from 'vue'

export default defineComponent({
  name: 'Checkbox',
  props: {
    modelValue: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      default: '',
    },
    toggleSwitch: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['update:modelValue', 'change'],
  setup(props, context) {
    const inputRef = ref(null)
    const inputValue = computed<boolean>({
      get(): boolean {
        return props.modelValue
      },
      set(newValue): void {
        if (props.readonly) {
          // return;
        }
        context.emit('update:modelValue', newValue)
        claimFocus()
      },
    })

    function claimFocus(): void {
      if (inputRef.value !== null) {
        ;(inputRef.value as unknown as HTMLInputElement).focus()
      }
    }

    function changeEvent(event: Event): void {
      if (props.readonly) {
        return
      }
      const checked = (event.target as HTMLInputElement).checked
      context.emit('change', checked)
    }

    function setValue(): void {
      if (props.readonly) {
        return
      }
      inputValue.value = !inputValue.value
    }

    return {
      inputValue,
      inputRef,
      setValue,
      changeEvent,
    }
  },
})
</script>
