import { ref, Ref } from 'vue'

interface IDefaultCountryCodeComposable {
  defaultCountryCode: Ref<string>
  updateDefaultCountryCode: (newCode: string | undefined) => void
}

const defaultCountryCode = ref<string>('us')

function updateDefaultCountryCode(newCode: string | undefined): void {
  if (newCode) {
    defaultCountryCode.value = newCode
  }
}

export function useDefaultCountryCode(): IDefaultCountryCodeComposable {
  return {
    defaultCountryCode,
    updateDefaultCountryCode,
  }
}
