<template>
  <div class="validated-field">
    <div class="label">{{ label }}</div>
    <input
      v-if="readonly && !modelValue"
      class="input short"
      part="input short"
      style="width: 100%"
      value="N/A"
      readonly
    />
    <input
      v-else
      class="input short"
      part="input short"
      :class="{ 'is-danger': errorMessage }"
      style="width: 100%"
      :placeholder="placeholder"
      v-model.number="reactiveValue"
      @input="errorMessage = ''"
      :readonly="readonly"
      type="number"
      inputmode="decimal"
      autocomplete="decimal"
      step="1"
      pattern="^[-/d]/d*$"
      @keypress="isInteger($event)"
    />
    <div class="error-message">{{ errorMessage }}</div>
  </div>
</template>
<script lang="ts">
import { useLocale } from '@/composables/useLocale'
import { computed, defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'Validated Integer',
  props: {
    modelValue: {
      type: Number,
    },
    placeholder: {
      type: String,
    },
    label: {
      type: String,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, context) {
    const { t } = useLocale()
    const reactiveValue = computed<number | undefined>({
      get(): number | undefined {
        return props.modelValue
      },
      set(newValue: number | undefined): void {
        if (typeof newValue !== 'number') {
          context.emit('update:modelValue', 0)
          return
        }
        context.emit('update:modelValue', newValue)
      },
    })

    const errorMessage = ref<string>('')

    function validate(): boolean {
      errorMessage.value = ''
      if (props.required && props.modelValue === undefined) {
        errorMessage.value = t('required')
        return false
      }
      const validInt = !props.modelValue || Number.isInteger(props.modelValue)
      if (!validInt) {
        errorMessage.value = t('no-decimals')
      }
      return validInt
    }

    function isInteger(event: KeyboardEvent): void {
      if (!/\d/.test(event.key) && event.key !== '-') {
        return event.preventDefault()
      }
    }

    return {
      reactiveValue,
      validate,
      errorMessage,
      isInteger,
    }
  },
})
</script>
