<template>
  <div class="validated-field">
    <div class="label">{{ label }}</div>
    <input
      v-show="readonly && !modelValue"
      class="input short"
      part="input short"
      style="width: 100%"
      value="N/A"
      readonly
    />
    <input
      v-show="!readonly || modelValue"
      class="input short"
      part="input short"
      :class="{ 'is-danger': errorMessage }"
      style="width: 100%"
      :placeholder="placeholder"
      v-model="reactiveValue"
      @input="errorMessage = ''"
      :readonly="readonly"
      autocomplete="tel"
      inputmode="tel"
      ref="phoneInputBox"
    />
    <div v-show="!readonly || modelValue" ref="controlContainer" style="width: 100%"></div>
    <div class="error-message">{{ errorMessage }}</div>
  </div>
</template>
<script lang="ts">
import { useDefaultCountryCode } from '@/composables/useDefaultCountryCode'
import { useLocale } from '@/composables/useLocale'
import intlTelInput from 'intl-tel-input'
import { computed, defineComponent, onMounted, ref, watch } from 'vue'

export default defineComponent({
  name: 'Validated String',
  props: {
    modelValue: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    label: {
      type: String,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, context) {
    const { t } = useLocale()
    const reactiveValue = computed<string | undefined>({
      get(): string | undefined {
        return props.modelValue
      },
      set(newValue: string | undefined): void {
        context.emit('update:modelValue', newValue)
      },
    })

    const errorMessage = ref<string>('')

    function validate(): boolean {
      errorMessage.value = ''
      if (props.required && props.modelValue === undefined) {
        errorMessage.value = t('required')
        return false
      }
      const isValidPhone = !props.modelValue || (phoneInput?.isValidNumber() ?? false)
      if (!isValidPhone) {
        errorMessage.value = t('invalid-phone')
      } else {
        const number = phoneInput?.getNumber()
        if (number) {
          context.emit('update:modelValue', phoneInput?.getNumber())
        }
      }
      return isValidPhone
    }

    const phoneInputBox = ref()
    const controlContainer = ref()

    const { defaultCountryCode } = useDefaultCountryCode()
    let phoneInput: intlTelInput.Plugin | undefined
    function showCountryCode() {
      phoneInput = intlTelInput(phoneInputBox.value, {
        initialCountry: phoneInput?.getSelectedCountryData().iso2 ?? defaultCountryCode.value,
        utilsScript: 'https://cdnjs.cloudflare.com/ajax/libs/intl-tel-input/17.0.8/js/utils.js',
        dropdownContainer: controlContainer.value,
        autoPlaceholder: 'aggressive',
      })
    }

    function hideCountryCode() {
      if (phoneInput === undefined) {
        return
      }
      phoneInput.destroy()
      phoneInput = undefined
    }

    onMounted(() => {
      if (!props.readonly) {
        showCountryCode()
      }
    })

    watch(
      () => props.readonly,
      () => {
        if (!props.readonly) {
          showCountryCode()
        } else {
          hideCountryCode()
        }
      },
    )

    return {
      reactiveValue,
      validate,
      errorMessage,
      phoneInputBox,
      controlContainer,
    }
  },
})
</script>
