<template>
  <div class="validated-field">
    <div class="label">{{ label }}</div>
    <input
      v-if="readonly && !modelValue"
      class="input short"
      part="input short"
      style="width: 100%"
      value="N/A"
      readonly
    />
    <input
      v-else
      class="input short"
      part="input short"
      :class="{ 'is-danger': errorMessage }"
      style="width: 100%"
      :placeholder="placeholder"
      v-model="reactiveValue"
      @input="errorMessage = ''"
      :readonly="readonly"
      autocomplete="email"
      inputmode="email"
    />
    <div class="error-message">{{ errorMessage }}</div>
  </div>
</template>
<script lang="ts">
import { useLocale } from '@/composables/useLocale'
import { isEmail } from '@/utils/formValidation'
import { computed, defineComponent, ref } from 'vue'

export default defineComponent({
  name: 'Validated String',
  props: {
    modelValue: {
      type: String,
    },
    placeholder: {
      type: String,
    },
    label: {
      type: String,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, context) {
    const { t } = useLocale()
    const reactiveValue = computed<string | undefined>({
      get(): string | undefined {
        return props.modelValue
      },
      set(newValue: string | undefined): void {
        context.emit('update:modelValue', newValue)
      },
    })

    const errorMessage = ref<string>('')

    function validate(): boolean {
      errorMessage.value = ''
      if (props.required && props.modelValue === undefined) {
        errorMessage.value = t('required')
        return false
      }
      const validLength = (props.modelValue?.length ?? 0) < 64
      if (!validLength) {
        errorMessage.value = t('64-letters-max')
        return false
      }
      const validEmail = !props.modelValue || isEmail(props.modelValue ?? '')
      if (!validEmail) {
        errorMessage.value = t('invalid-email')
      }
      return validEmail
    }

    return {
      reactiveValue,
      validate,
      errorMessage,
    }
  },
})
</script>
