<template>
  <div class="validated-field">
    <div class="label">{{ label }}</div>
    <checkbox v-model="reactiveValue" :readonly="readonly" />
    <div class="error-message"></div>
  </div>
</template>
<script lang="ts">
import { computed, defineComponent } from 'vue'
import Checkbox from '@/ui-components/Checkbox.vue'

export default defineComponent({
  name: 'Validated String',
  components: {
    Checkbox,
  },
  props: {
    modelValue: {
      type: Boolean,
    },
    placeholder: {
      type: String,
    },
    label: {
      type: String,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    required: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, context) {
    const reactiveValue = computed<boolean | undefined>({
      get(): boolean | undefined {
        return props.modelValue
      },
      set(newValue: boolean | undefined): void {
        context.emit('update:modelValue', newValue)
      },
    })

    function validate(): boolean {
      return true
    }

    return {
      reactiveValue,
      validate,
    }
  },
})
</script>
